
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { GetSpecialty, GetUserInfo, EditUserInfo } from 'src/Api/api'

export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
CellFormPreview,
CellBox,
Badge,
XHeader,
Actionsheet,
XInput,
XButton,
Checklist,
Checker,
CheckerItem,
XTextarea,
PopupRadio,
XAddress
},
data () {
return {
form: {
nickName: '',
name: '',
sex: '男',
tel: '',
company: '',
room: '',
position: '',
email: '',
weibo: '',
area: '',
description: '',
specialties: []
},
Specialties: [],
addressData: ChinaAddressV4Data,
showAddress: false,
area: []
}
},
created () {
GetSpecialty().then(response => {
console.log('GetSpecialty', response.data)
this.Specialties = response.data
})

console.log('addressData', this.addressData)
this.GetList()
},
methods: {
GetList () {
GetUserInfo({openid: this.$store.state.user.token}).then(response => {
console.log('GetUserInfo', response.data)
this.form = response.data.data.wxUser
console.log('this.form', this.form)
if (this.form.area) {
console.log('hwx', this.form.area.replace(/(\d\d\d\d)\d\d/ig, '$1' + '00'))
this.area = [this.form.area.replace(/(\d\d)\d\d\d\d/ig, '$1' + '0000'), this.form.area.replace(/(\d\d\d\d)\d\d/ig, '$1' + '00'), this.form.area]
}
var xx = this.form.specialties
var xx2 = xx.slice(0)
xx.splice(0, xx.length)
xx2.forEach(
function (value, index) {
xx.push({specialtyId: value.specialtyId, name: value.specialty.name})
})
})
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
},
logHide (str) {
console.log('on-hide', str)
console.log('area', this.area)
},
submit () {
if (this.area !== null && this.area.length === 3) {
this.form.area = this.area[2]
}
if (!this.form.nickName || !this.form.name || !this.form.sex || !this.form.tel || !this.form.company || !this.form.room) {
console.log('form', this.form)
console.log('提交失败：必填项未填')
this.$vux.alert.show({
title: '提交失败',
content: '必填项未填',
onShow () {
console.log('')
},
onHide () {
console.log('')
}
})
return
}
EditUserInfo({
model: this.form
}).then(response => {
console.log(response.data)
if (response.data.result === 0) {
var vm = this
this.$vux.alert.show({
title: '提示',
content: response.data.msg,
onShow () {
},
onHide () {
vm.$router.replace({
path: '/userinfo'
})
}
})
} else {
this.$vux.alert.show({
title: '提示',
content: response.data.msg,
onShow () {
},
onHide () {
}
})
}
})
}
}
}



